import NavBarContext from "../../context/NavBar";
import { useContext } from "react";
import { useSearchParams } from "react-router-dom";
const Pagination = () => {
  const Nbc = useContext(NavBarContext);
  let [params] = useSearchParams();
  const page = params.get("page");
  return (
    <>
      <div className="bg-color-01 page-pagination division">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <nav aria-label="Page navigation">
                <ul className="pagination justify-content-center">
                  {!page?Array(Math.round(Nbc.products.length / 8))
                    .fill({})
                    .map((p,i) => {
                      return (
                        <li key={i} className={`page-item ${i+1===Nbc.pageNum && 'active'}`}>
                          <a style={{cursor:'pointer'}}
                            className="page-link"
                            onClick={() => {
                              ChangePage(i+1);
                            }}
                          >
                            {i+1}
                          </a>
                        </li>
                      );
                    }):Array(Math.round(Nbc.products.filter(p=>p.category===page).length / 8))
                    .fill({})
                    .map((p,i) => {
                      return (
                        <li key={i} className={`page-item ${i+1===Nbc.pageNum && 'active'}`}>
                          <a style={{cursor:'pointer'}}
                            className="page-link"
                            onClick={() => {
                              ChangePage(i+1);
                            }}
                          >
                            {i+1}
                          </a>
                        </li>
                      );
                    })}

                 
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </div>
    </>
  );
  function ChangePage(pagenum) {
    Nbc.onChangePage(pagenum);
  }
};

export default Pagination;
