import OwlCarousel from "react-owl-carousel";

const SectionBrands1 = () => {
  return (
    <>
      <div id="brands-1" className="bg-lightgrey brands-section division">
        <div className="container">
          <div className="row">
            <div className="col text-center">       
                <OwlCarousel
                  items={1}
                  autoplay={true}
                  className="owl-theme"
                  loop
                  margin={10}
                  nav={false}
                  responsiveClass={true}
                  responsive={{
                    0: {
                        items: 2,
                    },
                    576: {
                        items: 3,
                    },
                    768: {
                        items: 4,
                    },
                    992: {
                        items: 5,
                    },
                    1000: {
                        items: 6,
            
                    }
                }}
                >

                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-11.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-12.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-13.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-14.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-15.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-16.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-17.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                  <div className="item" >
                    <div className="brand-logo">
                      <img
                        className="img-fluid"
                        src="images/brand-18.png"
                        alt="brand-logo"
                      />
                    </div>
                  </div>
                </OwlCarousel>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default SectionBrands1;
