


const ColMenuChild = ({image,href,title,desc}) => {
    return (  
        <div className="mega-menu-box">
        <div className="mega-menu-media">
            <div className="mega-menu-media-img">
                <div className="mega-menu-thumbnail">
                    <a href={href}>
                        <img src={image} alt={title}/>
                        <span></span></a>
                </div>
            </div>
            <div className="mega-menu-media-info">
                <h4 className="mega-menu-heading"><a href={href}>
                    {title}
                    <span></span></a></h4>
                <div className="mega-menu-desc">
                    {desc}
                </div>
            </div>
        </div>
    </div>

    );
}
 
export default ColMenuChild;