import logo from "./../../images/logo.png";
import MenuList from "./MenuList";
import React, { useState,useContext } from "react";
import MediaQuery from "react-responsive";
import {Link} from 'react-router-dom'
import NavBarContext from "../../context/NavBar";
const Header = () => {
  const [mobNavBaractive, setmobNavBaractive] = useState(false);
  const Nbc = useContext(NavBarContext);
  const UlFunc = () => {
    return (
      <ul className="rtl">
        <li className="basket-ico ico-30">
          <Link to="/cart">
            <span className="ico-holder">
              <span className="flaticon-shopping-bag"></span>
              <em className="roundpoint">{Nbc.basketRow.length}</em>
            </span>
          </Link>
        </li>

        <li>
          <Link to="/">صفحه اصلی</Link>
        </li>

        <li className="dropdown_menu">
          <Link to='/shop'>فروشگاه</Link>
          <ul>
            <li>
              <Link style={{cursor:"pointer"}}  to="/shop?page=burger">برگر</Link>
            </li>
            <li>
              <Link style={{cursor:"pointer"}} to="/shop?page=salad">سالاد</Link>
            </li>
            <li>
              <Link style={{cursor:"pointer"}} to="/shop?page=side">جانبی</Link>
            </li>
            <li>
              <Link style={{cursor:"pointer"}} to="/shop?page=dessert">دسر</Link>
            </li>
          </ul>
        </li>

        <li>
          <a href="#">تماس با ما</a>
        </li>
      </ul>
    );
  };
  return (
    <>
      <header
        id="header-1"
        className="header navik-header header-shadow center-menu-1 header-transparent viewport-lg header-transparent-on"
        style={{ marginTop: "0px" }}
      >
        <div className="container">
          <div className="navik-header-container">
            <div className="callusbtn">
              <a href="tel:123456789">
                <i className="fas fa-phone"></i>
              </a>
            </div>

            <div
              className="logo"
              data-mobile-logo={logo}
              data-sticky-logo={logo}
            >
              <a href="#hero-1">
                <img src={logo} alt="header-logo" />
              </a>
            </div>

            <div
              data-toggle="collapse"
              data-target="#navbarNavAltMarkup"
              aria-controls="navbarNavAltMarkup"
              aria-expanded="false"
              aria-label="Toggle navigation"
              className={
                mobNavBaractive ? " burger-menu menu-open" : "  burger-menu"
              }
              onClick={() => {
                mobNavBaractive
                  ? setmobNavBaractive(false)
                  : setmobNavBaractive(true);
              }}
            >
              <div className="line-menu line-half first-line"></div>
              <div className="line-menu"></div>
              <div className="line-menu line-half last-line"></div>
            </div>

            <nav className="navik-menu menu-caret navik-yellow">
              <MediaQuery minWidth={1199}>
                {(matches) =>
                  matches ? (
                    <MenuList />
                  ) : (
                    <div
                      className="navbar-collapse collapse"
                      id="navbarNavAltMarkup"
                    >
                      <MenuList />
                    </div>
                  )
                }
              </MediaQuery>

              <div className="logoCenter">
                <div
                  className="logo"
                  data-mobile-logo={logo}
                  data-sticky-logo={logo}
                >
                  <a href="/">
                    <img src={logo} alt="header-logo" />
                  </a>
                </div>
              </div>
              <MediaQuery minWidth={1199}>
                {(matches) =>
                  matches ? (
                    <UlFunc />
                  ) : (
                    <div
                      className="navbar-collapse collapse"
                      id="navbarNavAltMarkup"
                    >
                      <UlFunc />
                    </div>
                  )
                }
              </MediaQuery>
            </nav>
          </div>
        </div>

        <div className="header-shadow-wrapper"></div>
      </header>
    </>
  );
};

export default Header;
