import Footer from "../Footer/Footer";
import Header from "../Header/Header";
import Banner3 from "./Banner3";
import CartPage from "./CartPage";
import PageHero from "./PageHero";

const Cart = () => {

  return (
    <>
      <Header />
      <div className="page" id="page">
        <PageHero/>
        <CartPage/>
        <Banner3/>
      </div>
      <Footer />
    </>
  );
};

export default Cart;
