import React, { useState, useContext } from "react";
import NavBarContext from "../../context/NavBar";
const SectionMenu8 = () => {
  const [activeTab, setActivetab] = useState(4);
  function TabId(id) {
    setActivetab(id);
  }
  const Nbc = useContext(NavBarContext);
  function TabChild({ price, img, title, p,id}) {
    return (
      <>
        <div className="col-sm-6 col-lg-3">
          <div className="menu-7-item">
            <div className="menu-7-img rel">
              <img className="img-fluid" src={img} alt="menu-image" />

              <div className="menu-7-price bg-coffee">
                <h5 className="h5-xs yellow-color">{price} تومان</h5>
              </div>

              <div className="item-rating">
                <div className="stars-rating stars-md">
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star-half-alt"></i>
                </div>
              </div>

              <div className="like-ico ico-20">
                <a href="#">
                  <span className="flaticon-heart"></span>
                </a>
              </div>
            </div>

            <div className="menu-7-txt rel">
              <h5 className="h5-sm coffee-color">{title}</h5>

              <p className="grey-color">{p}</p>
{Nbc.basketRow.findIndex((p)=>p.id===id)===-1?(<a onClick={()=>{AddTobasket(id)}} className="btn btn-sm btn-tra-grey yellow-hover">
                <span className="flaticon-shopping-bag"></span>افزودن به سبد
              </a>):(<a onClick={()=>{RemoveFrombasket(id)}} className="btn btn-sm btn-tra-grey yellow-hover">
                <span className="flaticon-shopping-bag"></span>حذف از سبد
              </a>)}
              
            </div>
          </div>
        </div>
      </>
    );
  }
  function Tab({number}) {
    if(number==1){
      var category="burger"
    }
    if(number==2){
      var category="side"
    }
    if(number==3){
      var category="salad"
    }
    if(number==4){
      var category="dessert"
    }
    
    return (
      <>
      <div
              id={`tab-${number}`}
              className={`tab-content ${activeTab === number ? "current" : ""}`}
            >
              <div className="row">
                {Nbc.products.map((p,i)=>{
                  if(p.category==category){
                  return(<TabChild img={p.img} p={p.p} title={p.title} key={i} price={p.price} id={p.id}/>)
                  }
                })}


              </div>
            </div>
      </>
    );
  }

  return (
    <>
      <section id="menu-8" className="wide-70 menu-section division">
        <div className="container">
          <div id="tabs-nav">
            <div className="row">
              <div className="col-lg-12 text-center">
                <ul className="tabs-1 ico-55 red-tabs clearfix">
                  <li
                    className={`tab-link ${activeTab === 1 ? "current" : ""}`}
                    onClick={() => {
                      TabId(1);
                    }}
                  >
                    <span className="flaticon-burger"></span>
                    <h5 className="h5-sm">برگر</h5>
                  </li>

                  <li
                    className={`tab-link ${activeTab === 2 ? "current" : ""}`}
                    onClick={() => {
                      TabId(2);
                    }}
                  >
                    <span className="flaticon-fries"></span>
                    <h5 className="h5-sm">جانبی</h5>
                  </li>

                  <li
                    className={`tab-link ${activeTab === 3 ? "current" : ""}`}
                    onClick={() => {
                      TabId(3);
                    }}
                  >
                    <span className="flaticon-salad-1"></span>
                    <h5 className="h5-sm">سالاد</h5>
                  </li>

                  <li
                    className={`tab-link ${activeTab === 4 ? "current" : ""}`}
                    onClick={() => {
                      TabId(4);
                    }}
                  >
                    <span className="flaticon-doughnut"></span>
                    <h5 className="h5-sm">دسر</h5>
                  </li>
                </ul>
              </div>
            </div>
          </div>

          <div id="tabs-content">
           
                   {
                      Array(4).fill({}).map((p,i)=>{
                       return( <Tab key={i} number={i+1}/>)
                      })
                   }
          </div>
        </div>
      </section>
    </>
  );
  function AddTobasket(ProductId) {
    Nbc.onAddToBasket(ProductId);
  }
  function RemoveFrombasket(ProductId) {
    Nbc.onRemoveFromBasket(ProductId);
  }
};

export default SectionMenu8;
