import React, { useState } from 'react';
import OwlCarousel from "react-owl-carousel";
const MainSlider = () => {
  const[uCarousel,setuCarousel]=useState([
    {id:1,img:'images/slide-1.jpg',title:'انواع ساندویچ های برگر',p1:'ساعت کار:',p2:' 11:30 شب - 8:30 صبح'},
    {id:2,img:'images/slide-2.jpg',title:'تحویل سریع در کوتاه ترین زمان',p1:'شروع قیمت غذاهای ما :',p2:' از 25000 تومان'},
    {id:3,img:'images/slide-3.jpg',title:'همبرگهای بزرگ با قیمت مناسب',p1:'همین حالا سفارش دهید: ',p2:'021-123456789'},
  
  ])
  return (
    <section id="hero-1" className="hero-section division">
      <div className="slider">
        <OwlCarousel items={1} autoplay={true} className="owl-theme" loop margin={10} nav={false}>
         {uCarousel.map((p,i)=>{
          return( <div key={i} className="item">
          <img
            src={p.img}
            alt="slide-background"
            style={{background: `url(${p.img})`}}
          />
          <div className="caption d-flex align-items-center center-align">
            <div className="container">
              <div className="row">
                <div className="col-md-12">
                  <div className="caption-txt white-color rtl">
                    <div className="title">
                      <h2>{p.title}</h2>
                    </div>
                    <p>
                      {p.p1}
                      <span
                        className="yellow-color rtl"
                        style={{ fontFamily: "yekan_fa" }}
                      >
                       {p.p2}
                      </span>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>)
   
         })}
        </OwlCarousel>
      </div>
    </section>
  );
};

export default MainSlider;
