import ColMenu from "./ColMenu";
import React, { useState } from "react";
import MediaQuery from "react-responsive";

const MenuList = () => {
  const Ul = () => {
    return (
      <ul>
        <li>
          <div className="mega-menu-container">
            <div className="row">
              {MenuChild.map((c, i) => (
                <ColMenu key={i} info={c.data} />
              ))}
            </div>
          </div>
        </li>
      </ul>
    );
  };
  const [childactive, setChildAtive] = useState(false);
  const [MenuChild, setMenuChild] = useState([
    {
      id: 1,
      data: [
        {
          id: "1",
          img: "images/img-05.jpg",
          href: "#",
          title: "سیب زمینی و پنیر...",
          desc: "انواع مدل های سیب زمینی سرخ شده به اضافه پنیر پیتزا",
        },
        {
          id: "2",
          img: "images/img-01.jpg",
          href: "#",
          title: "میگو سوخاری ...",
          desc: "میگوهای سوخاری شده در طمع های مختلف",
        },
        {
          id: "3",
          img: "images/img-07.jpg",
          href: "#",
          title: "انواع سالادها...",
          desc: " انواع سالاد های در اندازه های کوچک و بزرگ",
        },
      ],
    },
    {
      id: 2,
      data: [
        {
          id: "1",
          img: "images/img-08.jpg",
          href: "",
          title: "منوی گیاهی...",
          desc: " انواع غذاهای گیاهی مناسب گیاهخواران",
        },
        {
          id: "2",
          img: "images/img-09.jpg",
          href: "",
          title: "غذای صبحانه...",
          desc: "انواع غذاهای مناسب وعده ی صبحانه",
        },
        {
          id: "3",
          img: "images/img-10.jpg",
          href: "",
          title: "سیب زمینی سرخ شده ...",
          desc: "سیب زمینی سرخ شده مخصوص با سبزیجات معطر",
        },
      ],
    },
  ]);

  return (
    <ul className="top-list" style={{ direction: "rtl" }}>
      <li>
        <a href="#">درباره ما</a>
      </li>

      <li className="mega-menu dropdown_menu">
        <a href="#">منوهای تستو</a>

        <MediaQuery minWidth={1199}>
          {(matches) =>
            matches ? (
              <Ul />
            ) : (
              <div className="navbar-collapse collapse" id="ColMenu"><Ul /></div>
            )
          }
        </MediaQuery>
        <span 
          data-toggle="collapse"
          aria-controls="ColMenu"
          data-target="#ColMenu"
          aria-expanded="false"
          aria-label="Toggle navigation"
          className={
            childactive ? "dropdown-plus dropdown-open" : "dropdown-plus"
          }
          onClick={() => {
            childactive ? setChildAtive(false) : setChildAtive(true);
          }}
        ></span>
      </li>

      <li>
        <a href="/login">ورود/ثبت نام</a>
      </li>
    </ul>
  );
};

export default MenuList;
