import { useContext } from "react";
import NavBarContext from "../../context/NavBar";
const CartPage = () => {
  const Nbc = useContext(NavBarContext);
  return (
    <>
      <section id="cart-1" className="wide-100 cart-page division">
        <div className="container">
          {Nbc.basketRow.length !== 0 ? (
            <>
              <div className="row">
                <div className="col-md-12">
                  <div className="cart-table mb-70">
                    <table
                      id="myTable"
                      style={{ direction: "rtl", textAlign: "right" }}
                    >
                      <thead>
                        <tr>
                          <th scope="col">محصول</th>
                          <th scope="col">قیمت</th>
                          <th scope="col">تعداد</th>
                          <th scope="col">جمع کل</th>
                          <th scope="col">حذف</th>
                        </tr>
                      </thead>

                      <tbody>
                        {Nbc.basketRow.map((p, i) => {
                          return (
                            <tr
                              key={i}
                              className={
                                i === Nbc.basketRow.length - 1 ? "last-tr" : ""
                              }
                            >
                              <td data-label="محصول" className="product-name">
                                <div className="cart-product-img">
                                  <img src={p.img} alt="cart-preview" />
                                </div>

                                <div className="cart-product-desc">
                                  <h5 className="h5-sm">{p.title}</h5>
                                  <p className="p-sm">{p.p}</p>
                                </div>
                              </td>

                              <td data-label="قیمت" className="product-price">
                                <h5 className="h5-md">{p.price} تومان</h5>
                              </td>
                              <td data-label="تعداد" className="product-qty">
                                <input onChange={(el)=>{Increment(el,p.id)}}
                                  className="qty"
                                  type="number"
                                  min="1"
                                  max="20"
                                  defaultValue={p.qnt}
                                  style={{ textAlign: "center" }}
                                />
                              </td>
                              <td
                                data-label="جمع کل"
                                className="product-price-total"
                              >
                                <h5 className="h5-md">{parseInt(p.price)*parseInt(p.qnt)} تومان</h5>
                              </td>
                              <td className="td-trash">
                                <i
                                  className="far fa-trash-alt"
                                  onClick={() => {
                                    RemoveFrombasket(p.id);
                                  }}
                                ></i>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                </div>
              </div>
              <div className="row rtl">
                <div className="col-lg-8">
                  <div className="discount-coupon row pt-15">
                    <div className="col-md-8 col-lg-7">
                      <form className="discount-form">
                        <div className="input-group">
                          <input
                            type="text"
                            className="form-control"
                            placeholder="کد تخفیف..."
                            id="discount-code"
                          />
                          <span className="input-group-btn">
                            <button
                              type="submit"
                              className="btn btn-salmon tra-salmon-hover"
                            >
                              افزودن کد تخفیف
                            </button>
                          </span>
                        </div>
                      </form>
                    </div>
                  </div>
                </div>

                <div className="col-lg-4">
                  <div className="cart-checkout bg-lightgrey">
                    <h5 className="h5-lg tr">جمع کل</h5>

                    <table className="tr">
                      <tbody>
                        <tr className="last-tr">
                          <td>مجموع</td>
                          <td> </td>
                          <td className="text-right">{M()} تومان</td>
                        </tr>
                      </tbody>
                    </table>

                    <a
                      href="#"
                      className="btn btn-md btn-salmon tra-salmon-hover"
                    >
                      ادامه خرید
                    </a>
                  </div>
                </div>
              </div>
            </>
          ) : (
            <div className="row">
              <div
                className="col-md-12 bg-light border border-light rounded"
                style={{ boxShadow: "0 2px 4px 0 #eee" }}
              >
                <div
                  className="p-5 w-100 "
                  style={{
                    textAlign: "center",
                    display: "flex",
                    flexFlow: "column wrap",
                    justifyContent: "center",
                  }}
                >
                  <span className="flaticon-shopping-bag cart-empty"></span>
                  <span style={{ fontSize: "22px" }}>
                    محصولی در سبد خرید وجود ندارد
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>
      </section>
    </>
  );
  function RemoveFrombasket(ProductId) {
    Nbc.onRemoveFromBasket(ProductId);
  }
  function M() {
    let m = 0;
    Nbc.basketRow.forEach((p) => {
      m += parseInt(p.price)*parseInt(p.qnt);
    });
    return m
  }
  function Increment(el,ProductId){
      Nbc.onInDE(ProductId,el.target.value)
  }
};

export default CartPage;
