import { Link } from "react-router-dom";
const PageHero = () => {
    return (<>
    <div id="cart-page" className="page-hero-section division">
        <div className="container">
            <div className="row">
                <div className="col-lg-10 offset-lg-1">
                    <div className="hero-txt text-center white-color">

                        
                        <div id="breadcrumb">
                            <div className="row">
                                <div className="col">
                                    <div className="breadcrumb-nav">
                                        <nav aria-label="breadcrumb">
                                            <ol className="breadcrumb rtl">
                                                <li className="breadcrumb-item"><Link to="/">خانه</Link></li>
                                                <li className="breadcrumb-item active" aria-current="page">سبد خرید</li>
                                            </ol>
                                        </nav>
                                    </div>
                                </div>
                            </div>
                        </div>

                        
                        <h2 className="h2-xl">سبد خرید</h2>

                    </div>
                </div>
            </div>	  
        </div>	   
    </div>
    </>
      );
}
 
export default PageHero;