import ColMenuChild from "./ColMenuChild";
import React, { useState } from "react";
const ColMenu = ({info}) => {
  
  return (
    <div className="col-md-6 col-lg-4">
      {info.map((c, i) => (
        <ColMenuChild key={i} image={c.img} href={c.href} title={c.title} desc={c.desc} />
      ))}
    </div>
  );
};

export default ColMenu;
