import React, { useEffect, useContext } from "react";
import { Fancybox as NativeFancybox } from "@fancyapps/ui/dist/fancybox.esm.js";
import "@fancyapps/ui/dist/fancybox.css";
import NavBarContext from "../../context/NavBar";
import { useSearchParams } from "react-router-dom";
const Menu6 = () => {
  let [params] = useSearchParams();
  const page = params.get("page");
  const Nbc = useContext(NavBarContext);
  function Fancybox(props) {
    const delegate = props.delegate || "[data-fancybox]";
    useEffect(() => {
      const opts = props.options || {};

      NativeFancybox.bind(delegate, opts);

      return () => {
        NativeFancybox.destroy();
      };
    }, []);

    return <>{props.children}</>;
  }
  function Menuchild({ price, img, title, p, id }) {
    return (
      <>
        <div className="col-sm-6 col-lg-3 ta-r">
          <div className="menu-6-item bg-white">
            <div className="menu-6-img rel">
              <div className="hover-overlay">
                <img className="img-fluid" src={img} alt="menu-image" />

                <span className="item-code bg-tra-dark">کد:{id}</span>

                <Fancybox options={{ infinite: false }}>
                  <div
                    data-fancybox="gallery"
                    data-src={img}
                    className="menu-img-zoom ico-25"
                  >
                    <a>
                      <span className="flaticon-zoom"></span>
                    </a>
                  </div>
                </Fancybox>
              </div>
            </div>

            <div className="menu-6-txt rel">
              <div className="item-rating">
                <div className="stars-rating stars-lg">
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star"></i>
                  <i className="fas fa-star-half-alt"></i>
                </div>
              </div>

              <div className="like-ico ico-25 ">
                <a href="#">
                  <span className="flaticon-heart"></span>
                </a>
              </div>

              <h5 className="h5-sm coffee-color">{title}</h5>

              <p className="grey-color">{p}</p>

              <div className="menu-6-price bg-coffee">
                <h5 className="h5-xs yellow-color">{price} تومان</h5>
              </div>

              <div className="add-to-cart bg-yellow ico-10">
                {Nbc.basketRow.findIndex((p) => p.id === id) === -1 ? (
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      AddTobasket(id);
                    }}
                  >
                    <span className="flaticon-shopping-bag"></span>
                    افزودن به سبد
                  </a>
                ) : (
                  <a
                    style={{ cursor: "pointer" }}
                    onClick={() => {
                      RemoveFrombasket(id);
                    }}
                  >
                    <span className="flaticon-shopping-bag"></span>
                    حذف از سبد
                  </a>
                )}
              </div>
            </div>
          </div>
        </div>
      </>
    );
  }
  return (
    <>
      <section id="menu-6" className="wide-70 menu-section division">
        <div className="container">
          <div className="row">
            {
              page
                ? Nbc.products
                    .filter((p) => p.category === page)
                    .slice((Nbc.pageNum - 1) * 8, Nbc.pageNum * 8)
                    .map((p, i) => {
                      return (
                        <Menuchild
                          key={i}
                          title={p.title}
                          img={p.img}
                          p={p.p}
                          price={p.price}
                          id={p.id}
                        />
                      );
                    })
                : Nbc.products
                    .slice((Nbc.pageNum - 1) * 8, Nbc.pageNum * 8)
                    .map((p, i) => {
                      return (
                      <Menuchild
                        key={i}
                        title={p.title}
                        img={p.img}
                        p={p.p}
                        price={p.price}
                        id={p.id}
                      />
                      );
                    })
              //  arr.slice((Nbc.pageNum-1)*8,Nbc.pageNum*8).map((p, i) => {
              //   return (
              //     <Menuchild
              //       key={i}
              //       title={p.title}
              //       img={p.img}
              //       p={p.p}
              //       price={p.price}
              //       id={p.id}
              //     />
              //   );
              // })
            }
          </div>
        </div>
      </section>
    </>
  );
  function AddTobasket(ProductId) {
    Nbc.onAddToBasket(ProductId);
  }
  function RemoveFrombasket(ProductId) {
    Nbc.onRemoveFromBasket(ProductId);
  }
};

export default Menu6;
